<!---->
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--    -->
      <el-row :gutter="30">
        <el-col :span="8">
          <el-input v-model="queryInfo.query" @clear="getGoodsList" placeholder="" clearable>
            <el-button @click="getGoodsList" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button @click="addGoodsPage" type="primary"></el-button>
        </el-col>
      </el-row>
      <!--    -->
      <el-table :data="goodslist" style="width: 100%" border stripe>
        <el-table-column type="index" label="ID" align="center"></el-table-column>
        <el-table-column prop="goods_name" label="" align="center"></el-table-column>
        <el-table-column prop="goods_price" label="()" align="center" width="110px"></el-table-column>
        <el-table-column prop="goods_weight" label="" align="center" width="85px"></el-table-column>
        <el-table-column label="" align="center" width="105px">
          <template v-slot="slotProp">
            {{ slotProp.row.add_time | dateFormat() }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180px" align="center">
          <template v-slot="slotProp">
            <el-button @click="editDialog(slotProp.row.goods_id)" type="primary" icon="el-icon-edit" size="mini">
            </el-button>
            <el-button @click="removeGoods(slotProp.row.goods_id)" type="danger" icon="el-icon-delete" size="mini">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page=this.queryInfo.pagenum
          :page-sizes="[5, 10, 20]"
          :page-size=queryInfo.pagesize
          layout="total, sizes, prev, pager, next, jumper"
          :total="total" background>
      </el-pagination>
    </el-card>
    <!--      -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="75%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="editForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="editForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="editForm.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="">
          <mavon-editor v-model="editForm.goods_introduce" :toolbars="toolbars" style="min-height:300px"/>
        </el-form-item>
        <el-form-item label="">
          <el-upload
              :headers="headerObj"
              :action="uploadUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              :file-list="fileList"
              list-type="picture">
            <el-button size="small" type="primary"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
        </el-form-item>
        <el-form-item v-for="item in manyTableData" :key="item.index" :label="item.attr_name" label-width="150px">
          <el-checkbox-group v-model="item.attr_vals">
            <el-checkbox v-for="(cb,i) in item.attr_vals" :key="i" :label="cb" border></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
        </el-form-item>
        <el-form-item v-for="item in onlyTableData" :key="item.index" :label="item.attr_name" label-width="230px">
          <el-input v-model="item.attr_value"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editGoodsInfo"> </el-button>
        </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
import axios from "axios";

export default {
  name: "list",
  data() {
    const checkParameter = (rule, value, callback) => {
      // 0
      if (value > 0) {
        return callback()
      } else {
        return callback(new Error('0'))
      }
    }
    return {
      // 
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodslist: [],
      // 
      total: 0,
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        goods_name: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ],
        goods_price: [
          {required: true, message: '', trigger: ['blur', 'change']},
          {validator: checkParameter, trigger: ['blur', 'change']}
        ],
        goods_number: [
          {required: true, message: '', trigger: ['blur', 'change']},
          {validator: checkParameter, trigger: ['blur', 'change']}
        ],
        goods_weight: [
          {required: true, message: '', trigger: ['blur', 'change']},
          {validator: checkParameter, trigger: ['blur', 'change']}
        ],
      },
      // markdown
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        mark: true, // 
        superscript: true, // 
        quote: true, // 
        ol: true, // 
        link: true, // 
        imagelink: true, // 
        help: true, // 
        code: true, // code
        subfield: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        /* 1.3.5 */
        undo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true // 
      },
      // 
      fileList: [],
      // 
      previewVisible: false,
      // 
      uploadUrl: axios.defaults.baseURL + 'upload',
      // 
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewPath: '',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    getGoodsList() {
      this.$http.get('goods', {params: this.queryInfo}).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.goodslist = result.data.goods
          this.total = result.data.total
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 
    removeGoods(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete('goods/' + id).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message({
              type: 'success',
              message: result.meta.msg
            });
            this.getGoodsList()
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    addGoodsPage() {
      this.$router.push('/goods/add')
    },
    // 
    editDialog(id) {
      this.editDialogVisible = true
      this.$http.get('goods/' + id).then(response => {
        console.log(response);//
        this.manyTableData=[]
        this.onlyTableData=[]
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.fileList = []
          result.data.pics.forEach(item => {
            const imgInfo = {
              name: item.pics_big.split("/")[3],
              url: item.pics_big_url
            }
            this.fileList.push(imgInfo)
          })
          result.data.attrs.forEach(item => {
            item.attr_vals = item.attr_vals.split(',')
            if (item.attr_sel === 'many') {
              this.manyTableData.push(item)
            } else {
              this.onlyTableData.push(item)
            }
          })
          this.editForm = result.data
          this.editForm.attrs = []
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    editGoodsInfo() {
      this.$refs.editFormRef.validate(valid => {
        if (valid) {
          // (addForm)
          const form = _.cloneDeep(this.editForm)
          this.manyTableData.forEach(item => {
            // console.log(item)
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals.join(',')
            }
            form.attrs.push(newInfo)
          })
          this.onlyTableData.forEach(item => {
            // console.log(item)
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals
            }
            form.attrs.push(newInfo)
          })
          this.editForm.pics.forEach(item => {
            console.log(item)
          })
          console.log(form)
          this.$http.put(`goods/${form.goods_id}`, form).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 200) {
              this.$message.success(result.meta.msg)
              this.getGoodsList()
              this.editDialogVisible = false
            } else if (result.meta.status === 400) {
              this.$message.error(result.meta.msg)
            } else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        } else {
          return this.$message.error("")
        }
      })
    },
    // 
    handlePreview(file) {
      this.previewPath = file.url
      this.previewVisible = true
    },
    // 
    handleRemove(file) {
      console.log(file)
      // const filePath = file.response.data.tmp_path
      const i = this.editForm.pics.findIndex(x => x.pic === file.name)
      this.editForm.pics.splice(i, 1)
    },
    // 
    handleSuccess(response) {
      console.log(response);//
      let result = response
      if (result.meta.status === 200) {
        this.$message.success(result.meta.msg)
        const picsInfo = {pic: result.data.tmp_path}
        this.editForm.pics.push(picsInfo)
      } else {
        this.$message.error('')
      }
    },
  }
}
</script>

<style lang="less" scoped>
.previewImg {
  width: 100%;
}

.el-checkbox {
  margin: 0 15px 15px 0 !important;
}
</style>
