<!---->
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!---->
      <el-alert
          title=""
          type="warning" :closable="false" show-icon>
      </el-alert>
      <!--      -->
      <el-row>
        <el-col>
          <span></span>
          <el-cascader
              v-model="selectKeys"
              :options="catelist"
              clearable
              :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name',children:'children'}"
              @change="parentCateChange"></el-cascader>
        </el-col>
      </el-row>
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" @click="addDialogVisible=true" :disabled="isBtnDisabled"></el-button>
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand" align="center">
              <template v-slot="slotProp">
                <el-tag v-for="(item,i) in slotProp.row.attr_vals" :key="i" @close="handleClose(i,slotProp.row)" closable>{{ item }}</el-tag>
                <el-input
                    class="input-new-tag"
                    v-if="slotProp.row.inputVisible"
                    v-model="slotProp.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(slotProp.row)"
                    @blur="handleInputConfirm(slotProp.row)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(slotProp.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="ID" type="index" align="center"></el-table-column>
            <el-table-column label="" prop="attr_name" align="center"></el-table-column>
            <el-table-column
                label="" sortable width="250" align="center">
              <template v-slot="slotProp">
                <el-button @click="editDialog(slotProp.row.attr_id)" type="primary" icon="el-icon-edit" size="mini">
                </el-button>
                <el-button @click="removeParams(slotProp.row.attr_id)" type="danger" icon="el-icon-delete" size="mini">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" @click="addDialogVisible=true" :disabled="isBtnDisabled"></el-button>
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand" align="center">
              <template v-slot="slotProp">
                <el-tag v-for="(item,i) in slotProp.row.attr_vals" :key="i" @close="handleClose(i,slotProp.row)" closable>{{ item }}</el-tag>
                <el-input
                    class="input-new-tag"
                    v-if="slotProp.row.inputVisible"
                    v-model="slotProp.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(slotProp.row)"
                    @blur="handleInputConfirm(slotProp.row)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(slotProp.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="ID" type="index" align="center"></el-table-column>
            <el-table-column label="" prop="attr_name" align="center"></el-table-column>
            <el-table-column
                label="" sortable width="250" align="center">
              <template v-slot="slotProp">
                <el-button @click="editDialog(slotProp.row.attr_id)" type="primary" icon="el-icon-edit" size="mini">
                </el-button>
                <el-button @click="removeParams(slotProp.row.attr_id)" type="danger" icon="el-icon-delete" size="mini">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
        :title="''+titleText"
        :visible.sync="addDialogVisible"
        width="50%" @close="addDialogClose">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogClose"> </el-button>
    <el-button type="primary" @click="addParams"> </el-button>
  </span>
    </el-dialog>
    <!--  -->
    <el-dialog
        :title="''+titleText"
        :visible.sync="editDialogVisible"
        width="50%" @close="editDialogClose">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogClose"> </el-button>
    <el-button type="primary" @click="editParams"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Params",
  data() {
    return {
      // 
      catelist: [],
      // 
      selectKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {},
      // 
      addFormRules: {
        attr_name: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    getCateList() {
      this.$http.get('categories').then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.catelist = result.data
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    getParamsDate() {
      if (this.selectKeys.length !== 3) {
        this.selectKeys = []
        this.manyTableData = []
        this.onlyTableData = []
      } else {
        this.$http.get(`categories/${this.cateId}/attributes`, {params: {sel: this.activeName}}).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message.success(result.meta.msg)
            // tag
            result.data.forEach(item => {
              if (item.attr_vals.length === 0) {
                item.attr_vals = []
              } else {
                item.attr_vals = item.attr_vals.split(',')
              }
              // tag
              item.inputVisible = false
              // tag
              item.inputValue = ''
            })
            if (this.activeName === 'many') {
              this.manyTableData = result.data
            } else {
              this.onlyTableData = result.data
            }
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }
    },
    // 
    parentCateChange() {
      this.getParamsDate()
    },
    // 
    handleTabClick() {
      this.getParamsDate()
    },
    // 
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
      this.addDialogVisible = false
    },
    // 
    addParams() {
      this.$refs.addFormRef.validate(valid => {
        if (valid) {
          this.$http.post(`categories/${this.cateId}/attributes`, {
                attr_name: this.addForm.attr_name,
                attr_sel: this.activeName
              }
          ).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 201) {
              this.$message.success(result.meta.msg)
              this.addDialogVisible = false
              // 
              this.getParamsDate()
              // 
            } else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        }
      })
    },
    // 
    editDialog(id) {
      this.$http.get(`categories/${this.cateId}/attributes/${id}`, {
        params: {attr_sel: this.activeName}
      }).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.editForm = result.data
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
      this.editDialogVisible = true
    },
    // 
    editDialogClose() {
      this.editDialogVisible = false
      this.$refs.editFormRef.resetFields()
    },
    // 
    editParams() {
      this.$refs.editFormRef.validate(valid => {
        if (valid) {
          this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
          }).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 200) {
              this.$message.success(result.meta.msg)
              this.editDialogVisible = false
              this.getParamsDate()
            } else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        }
      })
    },
    // 
    removeParams(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`categories/${this.cateId}/attributes/${id}`).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message({
              type: 'success',
              message: result.meta.msg
            });
            this.getParamsDate()
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // tag
    showInput(row) {
      row.inputVisible = true
      // 
      // $nextTick()
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // tag
    handleInputConfirm(row) {
      if(row.inputValue.trim().length === 0){
        row.inputValue = ''
        return
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.saveAttrVals(row)
    },
    // tag
    handleClose(id,row){
      row.attr_vals.splice(id,1)
      this.saveAttrVals(row)
    },
    // tag
    saveAttrVals(row){
      this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(',')
      }).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.editDialogVisible = false
          this.getCateList()
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    }
  },
  computed: {
    // truefalse
    isBtnDisabled() {
      if (this.selectKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    // ID
    cateId() {
      if (this.selectKeys.length === 3) {
        return this.selectKeys[2]
      } else {
        return null
      }
    },
    // 
    titleText() {
      if (this.activeName === 'many') {
        return ""
      } else {
        return ""
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-row {
  padding: 25px 0;
}
.input-new-tag{
  width: 150px!important;
}
</style>
