<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!---->
      <el-row>
        <el-col>
          <el-button @click="showAddCateDialog()" type="primary"></el-button>
        </el-col>
      </el-row>
      <!--    -->
      <el-table
          :data="catalist"
          row-key="cat_id"
          border
          lazy
          :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column
            type="index" label="ID" sortable align="center">
        </el-table-column>
        <el-table-column
            prop="cat_name" label="" sortable align="center">
        </el-table-column>
        <el-table-column
            label="" sortable align="center">
          <template v-slot="slotProp">
            <i class="el-icon-success" v-if="slotProp.row.cat_deleted === false"></i>
            <i class="el-icon-error" v-else></i>
          </template>
        </el-table-column>
        <el-table-column
            label="" sortable align="center">
          <template v-slot="slotProp">
            <el-tag size="mini" v-if="slotProp.row.cat_level === 0"></el-tag>
            <el-tag size="mini" type="success" v-else-if="slotProp.row.cat_level === 1"></el-tag>
            <el-tag size="mini" type="warning" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
            label="" sortable width="250" align="center">
          <template v-slot="slotProp">
            <el-button @click="editDialog(slotProp.row.cat_id)" type="primary" icon="el-icon-edit" size="mini">
            </el-button>
            <el-button @click="removeCate(slotProp.row.cat_id)" type="danger" icon="el-icon-delete" size="mini">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page=this.queryInfo.pagenum
          :page-sizes="[5, 10, 20]"
          :page-size=queryInfo.pagesize
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>

      <!--      -->
      <el-dialog title="" :visible.sync="addDialogVisible" @close="addDialogClosed" width="50%">
        <el-form :model="addCateForm" :rules="addCateRules" ref="addCateRef" label-width="100px">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-cascader
                v-model="selectKeys"
                :options="parentCateList"
                clearable
                :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name',children:'children',checkStrictly:'true' }"
                @change="parentCateChange"></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addCate"> </el-button>
              </span>
      </el-dialog>
      <!--      -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
        <el-form :model="editForm" :rules="addCateRules" ref="editFormRef" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false"> </el-button>
                <el-button type="primary" @click="editCate"> </el-button>
              </span>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
export default {
  name: "Cate",
  data() {
    return {
      // 
      catalist: [],
      // 
      total: 0,
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //
      addDialogVisible: false,
      // 
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      // 
      addCateRules: {
        cat_name: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ]
      },
      // 
      parentCateList: [],
      // id
      selectKeys: [],
      // 
      editForm: {},
      // 
      editDialogVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    getCateList() {
      // 
      this.$http.get('categories', {params: this.queryInfo}).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.catalist = result.data.result
          this.total = result.data.total
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // pagenum
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getCateList()
    },
    // 
    showAddCateDialog() {
      this.getParentCateList()
      this.addDialogVisible = true
    },
    // 
    getParentCateList() {
      this.$http.get('categories', {params: {type: 2}}).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.parentCateList = result.data
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    parentCateChange() {
      // console.log(this.selectKeys)
      if (this.selectKeys.length > 0) {
        this.addCateForm.cat_pid = this.selectKeys[this.selectKeys.length - 1]
        this.addCateForm.cat_level = this.selectKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addDialogClosed() {
      this.$refs.addCateRef.resetFields()
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
      this.selectKeys = []
    },
    // 
    addCate() {
      this.$refs.addCateRef.validate(valid => {
        if (valid) {
          this.$http.post('categories', this.addCateForm).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 201) {
              this.$message.success(result.meta.msg)
              this.addDialogVisible = false
              // 
              this.getCateList()
              // 
            } else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        }
      })
    },
    // 
    editDialog(id) {
      this.editDialogVisible = true
      this.$http.get('categories/' + id).then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.editForm = result.data
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    editCate() {
      this.$refs.editFormRef.validate(valid => {
        if (valid) {
          this.$http.put('categories/' + this.editForm.cat_id, {
            cat_name: this.editForm.cat_name,
          }).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 200) {
              this.$message.success(result.meta.msg)
              this.editDialogVisible = false
              this.getCateList()
            } else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        }
      })
    },
    // 
    removeCate(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete('categories/' + id).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message({
              type: 'success',
              message: result.meta.msg
            });
            this.getCateList()
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<style lang="less" scoped>
.el-icon-success {
  color: lightgreen;
}

.el-icon-error {
  color: red;
}
.el-cascader {
  width: 100%;
}
</style>
