<!---->
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!---->
      <el-alert
          title=""
          type="info" :closable="false" center show-icon>
      </el-alert>
      <!--      -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--    -->
      <el-form :model="addForm" :rules="rules" ref="addFormRef" label-width="200px" label-position="top">
        <el-tabs @tab-click="tabClicked" tab-position="left" v-model="activeIndex" :before-leave="beforeTabLeave">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                  v-model="addForm.goods_cat"
                  :options="catelist"
                  clearable
                  :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name',children:'children'}"
                  @change="parentCateChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox v-for="(cb,i) in item.attr_vals" :key="i" :label="cb" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
                :headers="headerObj"
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <mavon-editor v-model="addForm.goods_introduce" :toolbars="toolbars" style="min-height:500px"/>
            <el-button @click="addGoods" type="primary" class="btnAdd"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import _ from 'lodash'

export default {
  name: "Add",
  data() {
    return {
      // 
      activeIndex: 0,
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        // 
        attrs: []
      },
      // 
      rules: {
        goods_name: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ],
        goods_price: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ],
        goods_weight: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ],
        goods_number: [
          {required: true, message: '', trigger: ['blur', 'change']},
        ],
        goods_cat: [
          {required: true, message: "", trigger: "change"},
        ],
      },
      // 
      catelist: [],
      // tab
      tabCheck: true,
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      uploadUrl: axios.defaults.baseURL + 'upload',
      // 
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewPath: '',
      // 
      previewVisible: false,
      // markdown
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        mark: true, // 
        superscript: true, // 
        quote: true, // 
        ol: true, // 
        link: true, // 
        imagelink: true, // 
        help: true, // 
        code: true, // code
        subfield: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        /* 1.3.5 */
        undo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true // 
      },
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
// 
    getCateList() {
      this.$http.get('categories').then(response => {
        console.log(response);//
        let result = response.data
        if (result.meta.status === 200) {
          this.$message.success(result.meta.msg)
          this.catelist = result.data
        } else {
          this.$message.error('')
        }
      }).catch(response => {
        //
        this.$message.warning('')
        console.log(response)
      });
    },
    // 
    parentCateChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
        this.$message.error('')
      }
    },
    // tab
    beforeTabLeave(activeName, oldActiveName) {
      // console.log(activeName,oldActiveName)
      if (oldActiveName === '0') {
        this.$refs.addFormRef.validate(valid => {
          if (!valid) {
            this.$message.error('')
            this.tabCheck = false
          } else {
            this.tabCheck = true
          }
        })
        return this.tabCheck
      }
    },
    // tab
    tabClicked() {
      // 
      if (this.activeIndex === '1') {
        this.$http.get(`categories/${this.cateId}/attributes`, {params: {sel: 'many'}}).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message.success(result.meta.msg)
            result.data.forEach(item => {
              if (item.attr_vals.length === 0) {
                item.attr_vals = []
              } else {
                item.attr_vals = item.attr_vals.split(',')
              }
            })
            this.manyTableData = result.data
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }
      // 
      else if (this.activeIndex === '2') {
        this.$http.get(`categories/${this.cateId}/attributes`, {params: {sel: 'only'}}).then(response => {
          console.log(response);//
          let result = response.data
          if (result.meta.status === 200) {
            this.$message.success(result.meta.msg)
            this.onlyTableData = result.data
          } else {
            this.$message.error('')
          }
        }).catch(response => {
          //
          this.$message.warning('')
          console.log(response)
        });
      }
    },
    // 
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleRemove(file) {
      const filePath = file.response.data.tmp_path
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      this.addForm.pics.splice(i, 1)
    },
    // 
    handleSuccess(response) {
      console.log(response);//
      let result = response
      if (result.meta.status === 200) {
        this.$message.success(result.meta.msg)
        const picsInfo = {pic: result.data.tmp_path}
        this.addForm.pics.push(picsInfo)
      } else {
        this.$message.error('')
      }
    },
    // 
    addGoods() {
      this.$refs.addFormRef.validate(valid => {
        if (valid) {
          // (addForm)
          const form = _.cloneDeep(this.addForm)
          form.goods_cat = form.goods_cat.join(',')
          this.manyTableData.forEach(item => {
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals.join(',')
            }
            this.addForm.attrs.push(newInfo)
          })
          this.onlyTableData.forEach(item => {
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals
            }
            this.addForm.attrs.push(newInfo)
          })
          form.attrs = this.addForm.attrs
          console.log(form)
          this.$http.post('goods', form).then(response => {
            console.log(response);//
            let result = response.data
            if (result.meta.status === 201) {
              this.$message.success(result.meta.msg)
              this.$router.push('/goods')
            } else if(result.meta.status === 400){
              this.$message.error(result.meta.msg)
            }
            else {
              this.$message.error('')
            }
          }).catch(response => {
            //
            this.$message.warning('')
            console.log(response)
          });
        } else {
          return this.$message.error("")
        }
      })
    }
  },
  computed: {
    // ID
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-alert {
  margin-bottom: 20px;
}

.el-tabs {
  margin: 20px 0;
}

.el-cascader {
  width: 100%;
}

.el-checkbox {
  margin: 10px !important;
}

.previewImg {
  width: 100%;
}

.btnAdd {
  margin-top: 15px
}

</style>
